import Link from "../Link";
import SocialIcon from "../SocialIcon";

import "./index.css";

export default function MemberCard({photoSrc, name, instagramLink, title, description}) {
    return (
        <div className="member__card">
            <img className="member__card-avatar" src={photoSrc} alt="" />
            <div className="member__card-info">
                <p className="member__card-name">{name}</p>
                <Link href={instagramLink} targetBlank>
                    <div className="member__card-inst">
                        <SocialIcon socialName="instagram" colorMode="light"/>
                    </div>
                </Link>
            </div>
            <p className="member__card-title">{title}</p>
            <p className="member__card-description">{description}</p>
        </div>
    );
}