import { useState } from "react";
import { useTranslation } from "react-i18next";

import Anchor from "../../components/Anchor";
import HelloBanner from "../HelloBanner";
import Wrapper from "../../components/Wrapper";
import Navigation from "../Navigation";
import SocialBlock from "../../components/SocialBlock";
import LanguageSwitcher from "../../components/LanguageSwitcher";
import AnchorLink from "../../components/AnchorLink";

import { Squash as Hamburger } from "hamburger-react";

import "./index.css"

export default function Header() {
    const { t } = useTranslation();
    const [openMenu, setOpenMenu] = useState();

    const toggleMobileMenu = () => {
        setOpenMenu(!openMenu);
    }

    return (
        <header className="header">
            <HelloBanner />
            <Anchor id="top" />
            <Wrapper>
                <div className={`header__block ${openMenu ? 'header__block--menu-active' : ''}`}>
                    <AnchorLink anchorId="top">
                        <div className="header__logo-text">
                            {t("logoText")}
                        </div>
                    </AnchorLink>
                    <div className="header__navigation-block">
                        <Navigation toggleMobileMenu={toggleMobileMenu} />
                        <SocialBlock />
                        <LanguageSwitcher />
                    </div>
                    <Hamburger toggled={openMenu} toggle={setOpenMenu} onToggle={toggleMobileMenu} />
                </div>
            </Wrapper>
        </header>
    );
} 