import { useTranslation } from "react-i18next";

import Section from "../../components/Section";
import Wrapper from "../../components/Wrapper";
import SectionTitle from "../../components/SectionTitle";
import Card from "../../components/Card";

import Radio from "../../assets/img/radio.webp";
import Drone from "../../assets/img/drone.webp";
import Car from "../../assets/img/car.webp";

import "./index.css";

export default function Priorities() {
    const { t } = useTranslation();

    const PRIORITIES = [
        {
            title: t("connection"),
            description: t("connectionDescription"),
            imageSrc: Radio
        },
        {
            title: t("drones"),
            description: t("dronesDescription"),
            imageSrc: Drone
        },
        {
            title: t("vehicles"),
            description: t("vehiclesDescription"),
            imageSrc: Car
        }
    ];

    return (
        <Section colorMode="dark">
            <Wrapper>
                <SectionTitle colorMode="light">
                    {t("priorities")}
                </SectionTitle>
                <div className="priorities__block">
                    {PRIORITIES.map((priority, index) => {
                        return (
                            <Card
                                key={index}
                                title={priority.title}
                                description={priority.description}
                                imageSrc={priority.imageSrc}
                            />
                        );
                    })}
                </div>
            </Wrapper>
        </Section>
    );
}