import { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";

import Wrapper from "../../components/Wrapper";
import AnchorLink from "../../components/AnchorLink";

import "./index.css";

export default function HelloBanner() {
    const { t } = useTranslation();

    const useScrollDirection = () => {
        const [scrollDirection, setScrollDirection] = useState(null);

        useEffect(() => {
            let lastScrollY = window.scrollY;

            const updateScrollDirection = () => {
                const scrollY = window.scrollY;
                const direction = scrollY > lastScrollY ? "down" : "up";
                if (direction !== scrollDirection && (scrollY - lastScrollY > 20 || scrollY - lastScrollY < -20)) {
                    setScrollDirection(direction);
                }
                lastScrollY = scrollY > 0 ? scrollY : 0;
            };
            window.addEventListener("scroll", updateScrollDirection); // add event listener
            
            return () => {
                window.removeEventListener("scroll", updateScrollDirection); // clean up
            }
        }, [scrollDirection]);

        return scrollDirection;
    };

    return (
        <div className={`hello-banner ${useScrollDirection() === 'down' ? 'hello-banner--hidden' : ''}`}>
            <Wrapper>
                <h2>{t("hello-banner")}</h2>
                <AnchorLink anchorId="donation">
                    <span className="button--donate">
                        {t("donate")}
                    </span>
                </AnchorLink>
            </Wrapper>
        </div>
    )
}