import FacebookIcon from "../../assets/img/facebook.svg";
import InstagramIcon from "../../assets/img/instagram.svg";

import "./index.css";

export default function SocialIcon({socialName, colorMode}) {
    let iconHref;

    switch(socialName) {
        case "facebook": 
            iconHref = FacebookIcon;
            break;
        case "instagram": 
            iconHref = InstagramIcon;
            break;
        default: 
            console.log('Error. Social icon not found');
    }

    return(
        <svg className="social__icon" role="img" fill={colorMode === "light" ? "#fff" : ""}>
            <use href={`${iconHref}#${socialName}`} />
        </svg>
    );
}