import { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";

import "./index.css";

const SWITCHERS = [
	{ language: "uk" },
	{ language: "en" }
];

export default function LanguageSwitcher() {
	const { i18n } = useTranslation();
	const [lang, setLang] = useState(localStorage.getItem("selectedLanguage") || "uk");

	useEffect(() => {
		if (lang) {
			i18n.changeLanguage(lang);
			localStorage.setItem("selectedLanguage", lang);
		}
	}, [lang, i18n]);

	const handleChangeLanguage = (language) => {
		setLang(language);
	};

	return (
		<div className="language__switcher-block">
			<div className="language__switcher">
				{SWITCHERS.map(({ language }, index) => {
					return (
						<button
							key={index}
							onClick={() => handleChangeLanguage(language)}>
								<span className={`language__switcher-link ${lang === language ? "language__switcher-link--active" : ""}`}>
									{language}
								</span>
						</button>
					);
				})}
			</div>
		</div>
	);
};
