import { useTranslation } from "react-i18next";

import Section from "../../components/Section";
import Wrapper from "../../components/Wrapper";
import SectionTitle from "../../components/SectionTitle";
import MemberCard from "../../components/MemberCard";
import Anchor from "../../components/Anchor";

import MaxPhoto from "../../assets/img/max.jpg";
import AndriiPhoto from "../../assets/img/andrii.jpg";

import "./index.css";

export default function Team() {
    const { t } = useTranslation();

    const MEMBERS = [
        {
            name: t("teamMember1_name"),
            title: t("teamMember1_title"),
            description: t("teamMember1_description"),
            photo: MaxPhoto,
            instagram: "https://www.instagram.com/invertus/"
        },
        {
            name: t("teamMember2_name"),
            title: t("teamMember2_title"),
            description: t("teamMember2_description"),
            photo: AndriiPhoto,
            instagram: "https://www.instagram.com/a_sezko/"
        }
    ];

    return (
        <Section colorMode="dark">
            <Wrapper>
                <Anchor id="team"/>
                <SectionTitle colorMode="light">
                    {t("team")}
                </SectionTitle>
                <div className="team__block">
                    {MEMBERS.map((member, index) => {
                        return (
                            <MemberCard
                                key={index}
                                photoSrc={member.photo}
                                name={member.name}
                                instagramLink={member.instagram}
                                title={member.title}
                                description={member.description}>
                            </MemberCard>
                        );
                    })}
                </div>
                <p className="team__description">
                    {t("teamDescription")}
                </p>
            </Wrapper>
        </Section>
    );
}