import CTA from "../Link";
import SocialIcon from "../SocialIcon";

import "./index.css";

const SOCIALS = [
    {
        name: "facebook",
        link: "https://www.facebook.com/wild.volunteers.cf"
    },
    {
        name: "instagram",
        link: "https://www.instagram.com/wildvolunteers.cf/"
    }
];

export default function SocialBlock({ colorMode }) {
    return (
        <ul className="social__block">
            {SOCIALS.map((social, index) => {
                return (
                    <li className="social__item" key={index}>
                        <CTA href={social.link} targetBlank>
                            <div className="social__link">
                                <SocialIcon socialName={social.name} colorMode={colorMode} />
                            </div>
                        </CTA>
                    </li>
                );
            })}
        </ul>
    );
}