import { useContext } from "react";
import { ContentContext } from "../../providers/context/ContentContext";
import { useViewport } from "../../hooks/useViewport";

export default function AnchorLink({anchorId, children, onClickCallback }) {
    const [content, setContent] = useContext(ContentContext);

    const { width } = useViewport();
    const breakpoint = 767;

    if (anchorId) {
        const scrollToAnchor = () => {
            if (anchorId.indexOf('info') !== -1) {
                setContent('info');
                anchorId = "top";
            } else {
                setContent('main');
            }

            setTimeout(() => {
                if (typeof onClickCallback === 'function' && width <= breakpoint) onClickCallback();
        
                const offset = document.querySelector('.header').clientHeight;
                const el = document.querySelector(`#${anchorId}`);
                window.scroll({
                    top: (el.offsetTop - offset),
                    behavior: 'smooth'
                });
            }, 100)
        };

        return (
            // ContentContext.Provider is used for custom routing between Home and Info pages
            <ContentContext.Provider value={content}>
                <button onClick={scrollToAnchor}>
                    {children}
                </button>
            </ContentContext.Provider>
        );
    }
}