import { useTranslation } from "react-i18next";

import Slider from "react-slick";
import Section from "../../components/Section";
import Wrapper from "../../components/Wrapper";
import SectionTitle from "../../components/SectionTitle";
import TitledImage from "../../components/TitledImage";

import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";

import { logos } from "./logos";

import "./index.css";
import "../../styles/slick-overrides.css";

export default function Trusted() {
    const { t } = useTranslation();

    const sliderSettings = {
        dots: true,
        infinite: false,
        speed: 500,
        slidesToShow: 3,
        slidesToScroll: 3,
        initialSlide: 0,
        responsive: [
            {
                breakpoint: 9999,
                settings: "unslick"
            },
            {
                breakpoint: 991,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 3,
                    dots: true
                }
            }
        ]
    };

    return (
        <Section colorMode="light">
            <Wrapper>
                <SectionTitle colorMode="dark">
                    {t("ourMilitaryFriends")}
                </SectionTitle>
                <Slider className="trusted_list" {...sliderSettings}>
                    {logos.map((item, index) => {
                        return <TitledImage key={index} title={item.title} image={item.image} />
                    })}
                </Slider>
            </Wrapper>
        </Section>
    );
}