import { useTranslation } from "react-i18next";

import AnchorLink from "../../components/AnchorLink";

import "./index.css";

export default function Navigation({toggleMobileMenu}) {
    const { t } = useTranslation();

    const NAVIGATION = [
        { 
            title: t("reports"),
            link: "reporting" 
        },
        { 
            title: t("team"),
            link: "team" 
        },
        { 
            title: t("info"),
            link: "info"
        }
    ];

    return (
        <nav role="navigation">
            <ul className="nav__block">
                {NAVIGATION.map((navItem, index) => {
                    return (
                        <li key={index}>
                            <AnchorLink onClickCallback={toggleMobileMenu} anchorId={navItem.link}>
                                <span className="nav__link">
                                    {navItem.title}
                                </span>
                            </AnchorLink>
                        </li>
                    );
                })}
            </ul>
        </nav>
    );
}